.nav-links {
    text-decoration: none;
    font-size: 1.1em;
    color: var(--primary);
    // text-align: center;
    &:hover {
        color: var(--links-active);
        text-shadow: 1px 2px rgba(0, 0, 0, 0.1);
    }
    &:active {
        color: var(--links-active);
    }
}

.category-links {
    text-decoration: none;
    color: rgba(var(--primary-rgb), .5);
    &:hover {
        color: rgba(var(--primary-rgb), .9);
    }
    &:active, &:visited {
        color: rgba(var(--primary-rgb), .5);
    }
}

.post-links {
    text-decoration: none;
    color: var(--primary);
  
    &:hover {
        color: var(--links-active);
    }
    &:active{
        color: var(--primary);
    }
}

.tag-links {
    text-decoration: none;
    color: rgba(var(--primary-rgb), .6);
    &:hover {
        color: rgba(var(--primary-rgb), .9);
    }
    &:active, &:visited {
        color: rgba(var(--primary-rgb), .6);
    }
}

.orang-links {
    text-decoration: none;
    color: rgb(228, 118, 0);
    &:hover, &:active, &:visited {
        color: rgb(228, 118, 0);
    }
}